body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.MemoLine {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  height: 50px;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 10px;
}

.MemoLine_RepeatList {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  margin: 0 10px;
}

.MemoLine .MemoLine_input_container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.MemoLine .MemoLine_input_container .MemoLine_input {
  border: none;
  border-bottom: 1px solid gray;
  outline: none;
  font-size: 20px;
  margin-bottom: 3px;
  padding: 5px 0;
  background: transparent;
}

.MemoLine .MemoLine_input_container .MemoLine_delete {
  font-size: 12px;
  cursor: pointer;
  opacity: 0;
}

.MemoLine .MemoLine_input_container:hover .MemoLine_delete {
  opacity: 1;
}


.MemoLine .MemoLine_input_container .MemoLine_delete:hover {
  text-decoration: underline;
  color: #E57373;
}


.MemoLine .MemoLine_Trash_btn:hover {
  opacity: 0.9;
}

.dark .MemoLine .MemoLine_input {
  color: #EDEDEE;
}

.Checkbox {
    width: 32px;
    height: 32px;
    border: 1px solid #474957;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 0 5px;
    cursor: pointer;
    border-radius: 4px;
}

.Checkbox .Checkbox_active {
    width: 26px;
    height: 26px;
    background-color: #474957;
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 10px;
    border-radius: 4px;
}

.dark .Checkbox {
    border: 1px solid #EDEDEE;
}

.dark .Checkbox .Checkbox_active {
    background-color: #EDEDEE;
    color: #474957;
}





.App {
  height: 100%;
  width: 100vw;
  min-height: 100vh;
  overflow: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0 100px;
  box-sizing: border-box;
  direction: ltr;
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.App .App_add_btn {
  cursor: pointer;
  outline: none;
  width: 120px;
  height: 44px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 4px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  border: 1px solid rgba(0, 0, 0, 0.23);
  padding: 5px 16px;
  margin-bottom: 20px;
  font-size: 0.875rem;
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.App .App_add_btn:hover {
  background-color: rgba(0, 0, 0, 0.08);
}

.dark.App .App_add_btn {
  background-color: #474957;
  color: #EDEDEE;
  border-color: #EDEDEE;
}

.App.dark {
  background-color: #474957;
  color: #EDEDEE;
}

.App.rtl {
  direction: rtl
}

.ActionBar {
    position: fixed;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    top: 0;
    left: 0;
    width: 50px;
    padding: 20px;
    background-color: #fff;
}

.ActionBar .ActionBar_btn {
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    opacity: 0.2;
    -webkit-transition: opacity 0.1s ease-in;
    transition: opacity 0.1s ease-in;
    width: 50px;
    height: 50px;
}

.ActionBar .ActionBar_btn:hover {
    opacity: 0.9;
}

.dark .ActionBar {
    background-color: #474957;
}

.dark .ActionBar .ActionBar_btn {
    color: #EDEDEE;

}

.Header {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 50px 0;
}

.Header .Header_input {
  border: none;
  height: 50px;
  font-weight: bold;
  font-size: 45px;
  outline: none;
  width: 100%;
  text-align: center;
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.dark .Header .Header_input {
  background-color: #474957;
  color: #ededee;
}

